html, body {
    height: 100%;
    background-color: rgb(30, 35, 61);
}

.titleWrap {
    margin-top: 87px;
    font-size: 26px;
    font-weight: 700;
    color: 262626;
}

.contentWrap{
    margin-top: 26px;
    flex: 1;
}

.inputTitle {
    font-size: 12px;
    font-weight: 600;
    color: #262626;
}

.inputWrap{
    display: flex;
    border-radius: 8px;
    padding: 16px;
    margin-top: 8px;
    background-color: white;
    border: 1px solid #e2e0e0;
}

.inputWrap:focus-within {
    border: 1px solid #9e30f4;
}

.input {
    width: 100%;
    outline: none;
    border: none;
    height: 17px;
    font-size: 14px;
    font-weight: 400;

}

.input::placeholder {
    color: #dadada;
}

.errorMessageWrap {
    margin-top: 8px;
    padding-bottom: 10px;
    color: #ef0000;
    font-size: 12px;

}

.bottomButton {
    width: 100%;
    height: 48px;
    border: none;
    font-weight: 700;
    background-color: #9e30f4;
    border-radius: 64px;
    color: white;
    margin-bottom: 16px;
    cursor: pointer;
}

.bottomButton:disabled {
    background-color: #dadada;
    color: white;
    
}
