@import 'reset.css';

.root-wrap {
  position: absolute;
  top:0;
  bottom: 0;
  width: 0;
  height: 100%;
}

/* header 시작 */

.header-container {
  background-color: rgba(3, 37, 65, 1);
}

.header-wrap {
  height: 64px;
  max-width: 1400px;
  margin: 0 auto;

  display: flex;
  align-items: center;
}

.header-left-wrap{
  display: flex;
}

.header-left-wrap ul{
  display: flex;
  margin-left: 16px;
}

.header-left-wrap li{
  margin-right: 16px;
  padding: 8px;
}

.header-nav-item{
  color:white;
  text-decoration: none;
  font-weight: 600;
}

.page-container {
  max-width: 1400px;
  margin: 0 auto;
  padding: 32px 0;
  
}

body{
  background-color: rgb(30, 35, 61);
  color: white;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.movies-container{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.movie-container {
  width: 250px;
  margin: 16px;
  background-color: rgb(81, 96, 161);
  color: white;
  border-radius: 5px;
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.1);
  border: 1px solid rgb(104, 123, 139);
  
}

.movie-container img{
  max-width: 100%;
}

.movie-info{
  display: flex;
  padding: 20px;
  justify-content: space-between;
  align-items: center;
}

.movie-info h4 {
  margin: 0px;
}

.movie-info span {
  margin-left: 13px;
}
